<template>
  <div class="LearnEnglish">
    <h1>英文學習網</h1>
    <div class="container pt-2">
      <div class="row mt-3">
        <div class="col-4 p-3">
          <form>
            <div class="input-group mb-4">
              <div class="input-group-prepend">
                <span class="input-group-text" for="EnglishVocabulary">英文單字</span>
              </div>
              <input @keyup.enter="addEnglishList" v-model="vocalbulary" type="text" class="form-control" id="EnglishVocabulary" placeholder="請輸入英文單字" aria-describedby="emailHelp">
            </div>
            <div class="input-group mb-4">
              <div class="input-group-prepend">
                <span class="input-group-text" for="EnglishStatuS">單字詞性</span>
              </div>
              <input @keyup.enter="addEnglishList" v-model="partofspeech" type="text" class="form-control" id="EnglishStatus" placeholder="請輸入單字詞性" aria-describedby="emailHelp">
            </div>
            <div class="input-group mb-4">
              <div class="form-group-prepend">
                <span class="input-group-text" for="EnglishMeaning">中文意思</span>
              </div>
              <input @keyup.enter="addEnglishList" v-model="meaning" type="text" class="form-control" id="EnglishMeaning" placeholder="請輸入中文意思" aria-describedby="emailHelp">
            </div>
            <button @click="addEnglishList" type="button" class="btn btn-info">新增單字</button>
          </form>
        </div>
        <div class="col-8 p-3 border border-info rounded">
          <table class="table table-hover table-sm">
            <thead class="thead-dark">
              <tr>
                <th scope="col">#</th>
                <th scope="col">英文單字</th>
                <th scope="col">單字詞性</th>
                <th scope="col">中文意思</th>
                <th scope="col">刪除按鈕</th>
              </tr>
            </thead>
            <tbody>
                <tr v-for="(item, key) in EnglishList" :key=key>
                  <th scope="row">{{key+1}}</th>
                  <td>{{item.vocalbulary}}</td>
                  <td>{{item.partofspeech}}.</td>
                  <td>{{item.meaning}}</td>
                  <td>
                    <button @click="clearEnglishword(item, key)" type="button" class="btn btn-danger btn-sm" aria-label="Close">
                      <span class="text-light" aria-hidden="true">&times;</span>
                    </button>
                  </td>
                </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'EnglishLearn',
  data: function () {
    return {
      vocalbulary: '',
      partofspeech: '',
      meaning: '',
      EnglishList: [
        // {
        //   vocalbulary: 'get',
        //   partofspeech: 'v.',
        //   meaning: '得到',
        //   id: '1'
        // }
      ]
    }
  },
  created () {
    const getEnglishList = JSON.parse(localStorage.getItem('EnglishList'))
    this.EnglishList = getEnglishList || []
  },
  methods: {
    addEnglishList () {
      const vm = this
      const timestamp = Math.floor(Date.now())
      if (vm.vocalbulary.trim() !== '' && vm.partofspeech.trim() !== '' && vm.meaning.trim() !== '') {
        vm.EnglishList.push({
          vocalbulary: vm.vocalbulary,
          partofspeech: vm.partofspeech,
          meaning: vm.meaning,
          id: timestamp
        })
        vm.vocalbulary = ''
        vm.partofspeech = ''
        vm.meaning = ''
        localStorage.setItem('EnglishList', JSON.stringify(vm.EnglishList))
      }
    },
    clearEnglishword (item, key) {
      const vm = this
      vm.EnglishList.forEach(function (arr) {
        if (item.id === arr.id) {
          vm.EnglishList.splice(key, 1)
          localStorage.setItem('EnglishList', JSON.stringify(vm.EnglishList))
        }
      })
    }
  }
}
</script>
